/* Modal Header */

.modal-underbody {
    position: absolute;
    z-index: 99999999;
    display: flex;
    height: 100vh;
    width: 100vw;
    align-items: center;
    justify-content: center;
    --tw-bg-opacity: 1;
    background-color: rgba(0, 0, 0, var(--tw-bg-opacity));
    --tw-bg-opacity: 0.6;
    /* visibility: hidden; */
    opacity: 0;
    pointer-events: none;
    /* visibility: visible; */
    transition: opacity 300ms ease-in;
}

.modal-underbody.show {
    /* visibility: visible; */
    opacity: 1;
    pointer-events: all;
}

.modal-container-app {
    display: flex;
    width: 440px;
    flex-direction: column;
    border-radius: 0.25rem;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    opacity: 0;
    overflow: hidden;
    transition: opacity 100ms ease-in, margin-bottom 100ms ease-in, -webkit-transform 100ms ease-in;
    transition: transform 100ms ease-in, opacity 100ms ease-in, margin-bottom 100ms ease-in;
    transition: transform 100ms ease-in, opacity 100ms ease-in, margin-bottom 100ms ease-in, -webkit-transform 100ms ease-in;
}

.modal-container-app.show {
    -webkit-transform: scale(1);
            transform: scale(1);
    margin-bottom: 0;
    opacity: 1;
    max-height: -webkit-max-content;
    max-height: max-content;
    transition: opacity 150ms ease-in, margin-bottom 150ms ease-in, -webkit-transform 200ms ease-in;
    transition: transform 200ms ease-in, opacity 150ms ease-in, margin-bottom 150ms ease-in;
    transition: transform 200ms ease-in, opacity 150ms ease-in, margin-bottom 150ms ease-in, -webkit-transform 200ms ease-in;
}

.modal-container {
    display: flex;
    width: 440px;
    flex-direction: column;
    border-radius: 0.25rem;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
    margin-bottom: 20px;
    opacity: 0;
    transition: opacity 150ms ease-in-out, margin-bottom 150ms ease-in-out, -webkit-transform 150ms ease-in-out;
    transition: transform 150ms ease-in-out, opacity 150ms ease-in-out, margin-bottom 150ms ease-in-out;
    transition: transform 150ms ease-in-out, opacity 150ms ease-in-out, margin-bottom 150ms ease-in-out, -webkit-transform 150ms ease-in-out;
}

.modal-container.show {
    -webkit-transform: scale(1);
            transform: scale(1);
    margin-bottom: 0;
    opacity: 1;
}


.modal-form-header {
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.modal-header-title {
    font-size: 1.25rem;
    line-height: 1.75rem;
    font-weight: 700;
    --tw-text-opacity: 1;
    color: rgba(255, 255, 255, var(--tw-text-opacity));
    font-size: 27px;
    line-height: 30px;
    font-family: var(--font-primary);
}

.modal-header-desc {
    color: var(--colour-channels);
    font-family: var(--font-primary);
    font-size: 0.9rem;
    font-weight: 500;
    margin-top: 0.25rem
}

.modal-form-input-header.error-invite {
    --tw-text-opacity: 1;
    color: rgba(239, 68, 68, var(--tw-text-opacity));
}

.modal-form-header-app {
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    text-align: center;
}

.modal-form-header-app.small-header > .modal-header-title-app {
    font-family: var(--font-display);
    font-size: 20px;
