}

.modal-form-header-app.small-header {
    padding: 0 1rem 0 1rem;
    margin: 1rem 0 0 0;
    font-size: 18px;
}


.modal-header-title-app {
    font-weight: 700;
    --tw-text-opacity: 1;
    color: rgba(255, 255, 255, var(--tw-text-opacity));
    font-size: 20px;
    font-size: 27px;
    line-height: 30px;
