.embed-body {
    display: flex;
    flex-direction: row;
    border-radius: 0.2rem;
    background-color: #2f3136;
    /* height: 10rems; */
}

.side-color {
    display: flex;
    width: 0.25rem;
    min-height: 100%;
    border-radius: 0.25rem 0 0 0.25rem;
    background-color: aliceblue;
}

.embed-flex {
    display: flex;
    flex-direction: column;
    grid-gap: 0.75rem;
    gap: 0.75rem;
    padding: 0.5rem;
}

.embed-author {
    font-size: 0.8rem;
    font-weight: 400;
    color: var(--colour-primary);
}

.embed-title {
    font-size: 0.9rem;
    font-weight: 600;
    color: white;
}

.embed-link-title {
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    color: #10aff4;
}

.embed-link-title:hover {
    text-decoration: underline;
}

.embed-video {
    width: 400px;
    height: 225px;
    border-radius: 0.25rem;
}
.reminder {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: 15px;
    background: rgb(175,112,236);
    background: linear-gradient(90deg, rgba(175,112,236,1) 0%, rgba(225,145,170,1) 100%, rgba(0,212,255,1) 100%);
    font-family: var(--font-primary);
    font-weight: 500;
    color: #fdfafd;
    border-radius: 0.25rem 0 0 0;
    min-height: 2.3rem;
}
.reminder-container {
    display: flex;
    height: 100%;
    width: 100%;
    flex-direction: column;
}

.user-context-navbar-container {
    display: flex;
    height: 100%;
    width: 100%;
    flex-direction: row;
}

.reminder-title {
    position: absolute;
    margin: 0 auto;
}

.reminder-button {
    margin-left: 0.5rem;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    padding-top: 0.125rem;
    padding-bottom: 0.125rem;
    font-size: 0.875rem;
    line-height: 1.25rem;
    border-color: white;
    border-width: 1px;
    border-radius: 3px;
    transition: background-color 120ms ease-in, color 120ms ease-in;
}.reminder-button:hover{
    background-color: white;
    color: #df90ae;
}

.reminder-close {
    opacity: 0.5;
    margin-left: auto;
    cursor: pointer;
    margin-right: 0.5rem;
    height: 0.65rem;
    transition: opacity 150ms ease-in;
}.reminder-close:hover {
    opacity: 1;
}
.channel-bar-inner {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: calc(100% - 3.3125rem);
    margin-bottom: auto;
}

.friend-list-top {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 3.2rem;
    padding: 0.75rem 0.75rem;
    box-shadow: 0 1px 0 rgba(4,4,5,0.2),0 1.5px 0 rgba(6,6,7,0.05),0 2px 0 rgba(4,4,5,0.05);
}

.search-friend-list {
    width: 100%;
    height: 100%;
    border-radius: 0.2rem;
    cursor: pointer;
    background-color: var(--bg-tertiary);
}

/* Users Friends */

.user-friend-container {
    display: flex;
    flex-direction: column;
    grid-gap: 0.25rem;
    gap: 0.25rem;
    padding: 0.5rem;
    margin-top: 0.1rem;
    width: 100%;
    height: 100%;
}

.user-friend {
    display: flex;
    flex-direction: row;
    cursor: pointer;
    align-items: center;
    width: 100%;
    height: 2.625rem;
    border-radius: 0.2rem;
    transition: background-color 150ms ease-out;
}

.user-friend:hover { background-color: var(--bg-interaction-hover); }
.user-friend:hover > .user-friend-name { color: var(--colour-channels-hover); }

.user-friend.selected {
    background-color: var(--bg-interaction);
}

.user-friend-image {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 2rem;
    width: 2rem;
    border-radius: 100%;
    background-color: var(--bg-blurple);
    margin: 0 0.5rem;
}

.user-friend-image.icon { background-color: transparent; }

.user-friend-image-icon {
    width: 24px;
    height: 24px;
    color: var(--colour-primary);
}

.user-friend.selected > .user-friend-image.icon > .user-friend-image-icon { color: var(--colour-channels-hover); }

.user-friend-name {
    font-family: var(--font-primary);
    font-weight: 500;
    color: var(--colour-primary);
}

.user-friend.selected > .user-friend-name {
    font-family: var(--font-primary);
    font-weight: 500;
    color: white;
}

.user-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
    margin-top: 0.75rem;
    padding: 0 0 0 0.5rem;
    font-size: 12px;
    font-family: var(--font-display);
    font-weight: 500;
    color: var(--colour-channels);
}

.user-header-add {
    margin: 0 1rem 0 auto;
    transition: color 150ms ease-out;
}

.user-header-text {
    transition: color 150ms ease-out;
}

.user-header-add:hover, .user-header-text:hover  { color: var(--colour-channels-hover);}

.user-header-text:hover ~ .user-header-add { color: var(--colour-channels-hover);}

.no-direct-messages {
    fill: #37383f;
    margin: 1rem 0 0 0.5rem;
}
.invite-body {
    display: flex;
    width: 100vw;
    height: 100vh;
    position: absolute;
    left: 0;
    top: 0;
    justify-content: center;
    align-items: center
}
.login-register-inv {
    margin-right: auto;
    font-size: 14px;
    font-family: var(--font-primary);
    color: var(--colour-muted)
}
.register-button-inv {
    margin-top: 0.25rem;
    font-weight: 700;
    --tw-text-opacity: 1;
    color: rgba(11, 150, 206, var(--tw-text-opacity))
}

/* Modal Header */

.modal-underbody-inv {
    position: absolute;
    z-index: 2;
    display: flex;
    height: 100vh;
    width: 100vw;
    align-items: center;
    justify-content: center;
    --tw-bg-opacity: 1;
    background-color: rgba(0, 0, 0, var(--tw-bg-opacity));
    --tw-bg-opacity: 0.6
}

.modal-container-inv {
    display: flex;
    width: 440px;
    flex-direction: column;
    border-radius: 0.25rem
}

.modal-form-header-inv {
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center
}

.modal-header-title-inv {
    font-size: 1.25rem;
    line-height: 1.75rem;
    font-weight: 700;
    --tw-text-opacity: 1;
    color: rgba(255, 255, 255, var(--tw-text-opacity));
    font-size: 27px;
    line-height: 30px;
    font-family: var(--font-primary)
}

.modal-header-desc-inv {
    margin-bottom: 0.5rem;
    margin-top: 0.5rem;
    display: flex;
    color: var(--colour-channels);
    font-family: var(--font-primary);
    font-weight: 500;
    font-size: 14px
}

.modal-container-inv2 {
    display: flex;
    width: 440px;
    flex-direction: column;
    border-radius: 0.25rem
}

.modal-form-header-inv2 {
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-left: 3.5rem;
    padding-right: 3.5rem
}

.modal-header-title-inv2 {
    margin-bottom: 0.5rem;
    font-size: 1.25rem;
    line-height: 1.75rem;
    font-weight: 700;
    --tw-text-opacity: 1;
    color: rgba(255, 255, 255, var(--tw-text-opacity));
    font-size: 27px;
    line-height: 30px;
    font-family: var(--font-primary)
}

.modal-form-header-inv2 > .modal-header-desc-inv2 {
    display: flex;
    text-align: center;
    color: var(--colour-channels);
    font-family: var(--font-primary);
    font-weight: 500;
    font-size: 14px
}

/* Modal Slect Type */

.modal-select-container-inv {
    margin-top: 1.25rem;
    margin-bottom: 1.25rem;
    display: flex;
    flex-direction: column;
    padding-left: 1rem;
    padding-right: 1rem
}

.modal-select-header-inv {
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    width: 100%;
    font-size: 0.75rem;
    line-height: 1rem;
    font-family: var(--font-display);
    color: var(--colour-primary)
}

.modal-type-container-inv {
    display: flex;
    flex-direction: column;
    gap: 0.5rem
}

.modal-select-type-inv {
    display: flex;
    height: 3.5rem;
    cursor: pointer;
    flex-direction: row;
    align-items: center;
    gap: 0.5rem;
    border-radius: 0.25rem;
    padding-left: 0.75rem;
    padding-right: 0.75rem;
    background-color: var(--bg-secondary);
    color: var(--colour-primary);
    font-family: var(--font-primary)
}

.modal-selected-type-inv {
    background-color: var(--bg-tertiary);
}

.modal-select-hover-inv:hover {
    background-color: var(--bg-primary-alt);
}

.select-check-wrapper-inv {
    display: flex;
    height: 1.25rem;
    width: 1.25rem;
    align-items: center;
    justify-content: center;
    border-radius: 9999px;
    --tw-bg-opacity: 1;
    background-color: rgba(255, 255, 255, var(--tw-bg-opacity))
}

.select-check-inv {
    height: 1rem;
    width: 1rem;
    border-radius: 9999px;
    border-width: 3px;
    background-color: var(--bg-blurple);
    border-color: var(--bg-secondary)
}

.unselect-check-wrapper-inv {
    display: flex;
    height: 1.25rem;
    width: 1.25rem;
    align-items: center;
    justify-content: center;
    border-radius: 9999px;
    --tw-bg-opacity: 1;
    background-color: rgba(255, 255, 255, var(--tw-bg-opacity))
}

.unselect-check-inv {
    height: 1rem;
    width: 1rem;
    border-radius: 9999px;
    border-width: 3px;
    background-color: var(--bg-secondary);
    border-color: var(--bg-secondary)
}


.select-type-icon-inv {
    height: 1rem;
    width: 1rem
}

.select-type-info-inv {
    display: flex;
    flex-direction: column;
    gap: 2px
}

.select-type-name-inv {
    font-weight: 400;
    font-family: var(--font-primary);
    line-height: 16px;
    color: var(--colour-normal)
}

.select-type-desc-inv {
    line-height: 12px
}

/* Modal Input */

.modal-form-input-container-inv {
    margin-top: 1.25rem;
    margin-bottom: 1.25rem;
    display: flex;
    flex-direction: column;
    padding-left: 1rem;
    padding-right: 1rem
}

.modal-form-input-header-inv {
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    width: 100%;
    font-size: 0.75rem;
    line-height: 1rem;
    font-family: var(--font-display);
    color: var(--colour-primary)
}

.modal-form-input-inv {
    height: 2.5rem;
    border-radius: 0.25rem;
    border-width: 1px;
    --tw-border-opacity: 1;
    border-color: rgba(0, 0, 0, var(--tw-border-opacity));
    --tw-border-opacity: 0.3;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    outline: 2px solid transparent;
    outline-offset: 2px;
    background-color: var(--bg-secondary);
    color: var(--colour-normal);
    font-weight: 100;
    font-family: var(--font-primary)
}

.modal-form-input-inv::-webkit-input-placeholder {
    color: hsla(0, 0%, 100%, 0.3);
}

.modal-form-input-inv:-ms-input-placeholder {
    color: hsla(0, 0%, 100%, 0.3);
}

.modal-form-input-inv::placeholder {
    color: hsla(0, 0%, 100%, 0.3);
}

.modal-form-input-inv:hover {
    --tw-border-opacity: 0.8
}

/* Options */

.options-container-inv {
    margin-top: auto;
    display: flex;
    height: 70px;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    gap: 0.5rem;
    border-radius: 0.25rem;
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
    padding-left: 1.25rem;
    padding-right: 1.25rem
}

.submit-button-inv {
    margin-left: 1.5rem;
    margin-right: 1.5rem;
    margin-bottom: 1.5rem;
    margin-top: 1rem;
    height: 38px;
    border-radius: 0.25rem;
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 0.875rem;
    line-height: 1.25rem;
    --tw-text-opacity: 1;
    color: rgba(255, 255, 255, var(--tw-text-opacity));
    background-color: var(--bg-blurple);
    font-family: var(--font-primary);
    font-weight: 500;
    transition: background-color 150ms ease-in
}

.submit-button-inv:hover {
    background-color: var(--bg-blurple-hover);
}

.cancel-button-inv {
    height: 38px;
    border-radius: 0.25rem;
    font-size: 0.875rem;
    line-height: 1.25rem;
    --tw-text-opacity: 1;
    color: rgba(255, 255, 255, var(--tw-text-opacity));
    font-family: var(--font-primary);
    padding-right: 1rem;
    padding-left: 0.4rem
}

.cancel-button-inv:hover {
    text-decoration: underline;
}
